/* COLOR VARIABLES */
:root {
    --black: #131516;
    --neonblue: #12d0f1; 
    --brightNeonBlue: #32e0ff; 
}


/* HEADER STYLE */
header {
    display: flex;
    padding: 0px;
    background: var(--black); 
    
    /* justify-content: space-between; */
    width: 100vw;
    /* height: auto; */

    
   
}

/* HEADER H1 STLYE */
 h1 {
    background: var(--neonblue);
    padding: 15px;
    font-size: 2.0rem;
    /* position: absolute;  */
    text-align: center;
    border-radius: 4px;
     
}

.myName {
  display: flex;  
  position: absolute;
    /* font-family: 'Neonderthaw'; */
    font-size: 40px;
    color: var(--brightNeonBlue);
    /* background: var(--black); */
    letter-spacing: 1px;
    text-shadow: 
        0 0 2px #000,
        0 0 5px #ffffff00, 
        0 0 10px #ffffff00, 
        0 0 20px #0ff, 
        0 0 40px #0ff,
        0 0 50px #0ff;
    
    background-color: var(--black);
    padding: 7px;
    margin-left: 3vw;
    margin-top: 1.5%;   
    border-radius: 5px;
    box-shadow: 0 0 15px 2px #32e0ff;      
        
        /* 0 0 80px #0ff, 
        0 0 90px #0ff, 
        0 0 100px #0ff, 
        0 0 150px #0ff;  */
}

/* individual letter flickering */
.myName span {
    animation: flicker 500ms linear;
}

.myName .delay1 {
    animation-delay: 1.8s;
}

.myName .delay2 {
    animation-delay: 2.4s;
}

@keyframes flicker {
    0% {opacity: 0;}
    10% {opacity: 1;}
    20% {opacity: 0;}
    40% {opacity: 1;}
    50% {opacity: 0;}
    60% {opacity: 1;}
    80% {opacity: 0;}
    100% {opacity: 1;}
}

img {
    width: 100%;
    height: 220px; 

}


